body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  display: block;
  margin: 0 auto;
  max-width: 1250px;
  height: auto;
  // width: 100vw;
  // height: 100vh;
  background-color: #E7F6FF;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  display: block;
  margin: 0 auto;
  max-width: 1080px;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  background: #fff;
  border-radius: 20px;


  .content {
    display: block;
    padding: 40px;
    align-items: end;
    .content-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    .search-block {
      display: flex;
      border: 1px solid #f3f3f3;
      border-radius: 10px;
      padding: 0 15px;

      input {
        width: 150px;
        border: 0;
        padding: 13px;
        font-size: 15px;
        outline: none;
      }
      .button {
        display: flex;
        justify-content: center;
        position: relative;
        top: 14px;
        align-items: center;
        background: #FFFFFF;
        border: 1px solid #f2f2f2;
        box-sizing: border-box;
        border-radius: 4px;
        height: 14px;
        width: 14px;
        cursor: pointer;
        opacity: 0.6;
        transition: opacity 0.15s ease-in-out;
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}